.SideNav {
  list-style-type: none;
  font-size: 24px;
  text-align: center;
  margin: 20px 0;
}
.errorText {
  text-align: center;
}
ul {
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
}
.placeholder {
  height: 200px;
  width: 200px;
  border: solid white 1px;
  border-radius: 100%;
  background-image: url("https://i.postimg.cc/C5jTLnxj/me.jpg");
  background-size: cover;
  background-position: center center;
}
.footer {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.SideNav:hover {
  transform: scale(1.5);
  background-color: skyblue;
}
.errorImg {
  max-width: 500px;
  max-height: 1000px;
  margin: 0 auto;
}
.errorDiv {
  margin-top: 150px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
h1 {
  text-align: center;
  margin-top: 50px;
  font-family: "Roboto Slab", serif;
}
main {
  margin-left: 33%;
  min-height: 100vh;
  /* background:linear-gradient(to right, white, whitesmoke); */
  background-size: cover;
}
.links {
  text-decoration: none;
  color: white;
  font-family: "Roboto Slab", serif;
}
.nameTitle {
  font-size: 22px;
  margin-top: 10px;
  font-family: "Roboto Slab", serif;
}
.logosAndPhoto {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  bottom: 3%;
}
.componentNav {
  position: relative;
  top: 2%;
}
.logos {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
  top: 35px;
}
.logo {
  margin: 0 8px;
  transform: scale(1.5);
}
.logo:hover {
  opacity: 0.5;
}
.logoLinks {
  color: white;
}
.headerLinks {
  color: white;
  text-decoration: none;
  list-style: none;
  margin: 15px;
  font-family: "Roboto Slab", serif;
  font-size:20px;
}
.headerLinks:hover {
  background-color: skyblue;
  transform: scale(1.3);
}
.headerLogoLinks {
  color: white;
}
@media (max-width: 700px) {
  .SideNav {
    font-size: 24px;
  }
  .SideNav:hover {
    transform: scale(1.1);
  }
  .errorImg {
    max-width: 350px;
  }
}
@media (max-width: 650px) {
  .placeholder {
    width: 125px;
    height: 125px;
  }
  .nameTitle {
    font-size: 14px;
  }
  .logosAndPhoto {
    margin-top: -50%;
  }
}
@media (max-width: 500px) {
  main {
    margin-left: 0;
  }
  .logos {
    top: 0;
    left: 50px;
  }
}
@media (max-width:455px){
  .headerLinks{
    font-size:16px;
  }
  .logos {
    left: 30px;
  }
}
@media (max-width: 400px) {
  .placeholder {
    width: 100px;
    height: 100px;
  }
  .nameTitle {
    font-size: 10px;
  }
  .logosAndPhoto {
    margin-top: -225%;
  }
  .headerLinks{
    font-size:14px;
  }
  .logos {
    left: 30px;
  }
}
@media (max-width:375px){
  .headerLinks{
    font-size:12px;
  }
  .logos {
    left: 30px;
  }
}
@media (max-width:350px){
  .headerLinks{
    font-size:10px;
  }
  .logos {
    left: 10px;
  }
}
